
import { defineComponent, ref, watch, toRefs, nextTick } from 'vue'

export default defineComponent({
  inheritAttrs: false,
  props: {
    multiline: {
      type: Boolean,
      default: false,
    },
    modelValue: {
      type: String,
      default: '',
    },
    error: {
      type: Boolean,
      default: false,
    },
    autoExpand: {
      type: Boolean,
      default: false,
    },
    baseTextareaHeight: {
      type: Number,
      default: 56,
    },
  },
  emits: {
    'update:modelValue': null,
    blur: null,
    focus: null,
  },
  setup(props, context) {
    const textarea = ref<HTMLElement | null>(null)
    const resizeTextareaIfAutoExpand = () => {
      if (textarea.value && props.autoExpand) {
        textarea.value.style.height = ''
        textarea.value.style.height = `${Math.max(
          textarea.value.scrollHeight,
          props.baseTextareaHeight
        )}px`
      }
    }

    const onInput = (e: Event) => {
      context.emit('update:modelValue', (e.target as HTMLInputElement).value)
    }

    const { modelValue } = toRefs(props)
    watch(
      modelValue,
      async () => {
        await nextTick()
        resizeTextareaIfAutoExpand()
      },
      { immediate: true }
    )

    return {
      textarea,
      onInput,
    }
  },
})
